<script>
import container from "./components/Container.vue";
import userData from "./data";
// import axios from "axios";

export default {
  name: "App",
  components: {
    Container: container,
  },
  mounted() {
    this.emitter.on("boxClick", (a) => {
      this.selectFilter = a;
    });
  },
  data() {
    return {
      Udata: userData,
      counter: 0,
      step: 0,
      urlimage: "",
      myWrite: "",
      myAuthor: "",
      selectFilter: "",
    };
  },
  methods: {
    // more() {
    //   if (this.counter == 0) {
    //     axios
    //       .get("https://codingapple1.github.io/vue/more1.json")
    //       .then((result) => {
    //         console.log(result.data);
    //         this.Udata.push(result.data);
    //       });
    //     this.counter++;
