    upload(e) {
      let files = e.target.files;
      console.log(files[0]);
      let url = URL.createObjectURL(files[0]);
      this.urlimage = url;
      this.step++;
    },
    publish() {
      var myPost = {
        name: this.myAuthor,
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.urlimage,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.myWrite,
        filter: this.selectFilter,
      };
      this.Udata.unshift(myPost);
      this.step = 0;
    },
