import axios from "axios";
import { createStore } from "vuex";

const store = createStore({
  state() {
    return {
      name: "kim",
      age: 20,
      likes: 1,
      likesClick: false,
      more: {},
    };
  },
  mutations: {
    이름변경(state) {
      state.name = "park";
    },
    나이변경(state, payload) {
      state.age += payload;
    },
    좋와요(state) {
      state.likes++;
      // if (state.likesClick == false) {
      //   state.likes++;
      //   state.likesClick = true;
      // } else {
      //   state.likes--;
      //   state.likesClick = false;
      // }
    },
    setMore(state, data) {
      state.more = data;
    },
  },
  actions: {
    //ajax요청
    getData(context) {
      axios.get("https://codingapple1.github.io/vue/more1.json").then((a) => {
        console.log(a.data);
        context.commit("setMore", a.data);
      });
    },
  },
});

export default store;
