<template>
  <div v-if="step == 0">
    <Post :Cdata="Udata[i]" v-for="(a, i) in Udata" :key="i" />
  </div>

  <!-- 필터선택페이지 -->
  <div v-if="step == 1">
    <div
      :class="selectFilter"
      class="upload-image"
      :style="`background-image:url(${urlimage})`"
    ></div>
    <div class="filters">
      <FilterBox
        :filter1="filter1"
        :urlimage="urlimage"
        v-for="filter1 in filters"
        :key="filter1"
        >{{ filter1 }}</FilterBox
      >
    </div>
  </div>

  <!-- 글작성페이지 -->
  <div v-if="step == 2">
    <div
      :class="selectFilter"
      class="upload-image"
      :style="`background-image:url(${urlimage})`"
    ></div>
    <div class="write">
      <textarea @input="$emit('write', $event.target.value)" class="write-box">
write!</textarea
      >
    </div>
    <div class="author">
      글작성자:
      <textarea @input="$emit('author', $event.target.value)"></textarea>
    </div>
  </div>
</template>

<script>
import post from "./Post.vue";
import FilterBox from "./FilterBox.vue";

export default {
  name: "container_vue",
  components: {
    Post: post,
    FilterBox,
  },
  mounted() {
    this.emitter.on("boxClick", (a) => {
      this.selectFilter = a;
    });
  },
  props: {
    Udata: Array,
    step: Number,
    urlimage: String,
  },
  data() {
    return {
      selectFilter: "",
      filters: [
        "aden",
        "_1977",
        "brannan",
        "brooklyn",
        "clarendon",
        "earlybird",
        "gingham",
        "hudson",
        "inkwell",
        "kelvin",
        "lark",
        "lofi",
        "maven",
        "mayfair",
        "moon",
        "nashville",
        "perpetua",
        "reyes",
        "rise",
        "slumber",
        "stinson",
        "toaster",
        "valencia",
        "walden",
        "willow",
        "xpro2",
      ],
    };
  },
};
</script>

<style>
.wordText {
  color: rgb(50, 196, 31);
}
.author {
  font-size: 13px;
  margin-left: 10px;
  margin-bottom: 10px;
}
</style>
